<template>
  <div id="app">
    <Navbar />
    <div class="router-view-container">
      <router-view @imagesLoaded="handleImagesLoaded" />
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar.vue';
import { startConfetti, stopConfetti } from './utils/confettiEffect.js';

export default {
  components: { Navbar },
  data() {
    return {
      imagesLoaded: false // 
    };
  },
  
  mounted() {
    // 
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
    
    // 
    this.$router.afterEach(() => {
      this.scrollToTop();
    });
    
    // 
    // handleImagesLoaded
  },
  
  beforeUnmount() {
    // 
    this.stopConfettiEffect();
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
  },
  
  methods: {
    startConfettiEffect() {
      startConfetti();
    },
    
    stopConfettiEffect() {
      stopConfetti();
    },
    
    handleVisibilityChange() {
      if (document.hidden) {
        this.stopConfettiEffect();
      } else {
        // 
        if (this.imagesLoaded) {
          this.startConfettiEffect();
        }
      }
    },
    
    handleImagesLoaded() {
      //   
      //  startConfetti()/enableMouseTrail()
      this.imagesLoaded = true;
    },
    
    scrollToTop() {
      // 
      if (typeof window !== 'undefined') {
        // 1: 
        window.scrollTo(0, 0);
        
        // 2: requestAnimationFrame
        requestAnimationFrame(() => {
          window.scrollTo(0, 0);
        });
        
        // 3: DOM
        setTimeout(() => {
          window.scrollTo(0, 0);
          
          // 4: scrollIntoView
          const appElement = document.getElementById('app');
          if (appElement) {
            appElement.scrollIntoView({ behavior: 'instant' });
          }
          
          // 5: scrollTop
          if (document.documentElement) {
            document.documentElement.scrollTop = 0;
          }
          if (document.body) {
            document.body.scrollTop = 0;
          }
        }, 100);
      }
    }
  }
};
</script>

<style>
/*
  
  - Firefox: scrollbar-width: none;
  - IE/Edge (): -ms-overflow-style: none;
  - WebKit (Chrome/Safari/Opera):  scrollbar / 0
   htmlbody  #app
*/
html, body, #app {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

/* WebKit browsers (Chrome, Safari, newer Edge Chromium, Opera) */
html::-webkit-scrollbar,
body::-webkit-scrollbar,
#app::-webkit-scrollbar {
  width: 0;
  height: 0;
}

body {
  margin: 0;
  background: #111;
  color: #fff;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.2em;
  overflow-x: hidden;
  cursor: default;
}

html, body {
  height: 100%;
  overflow-x: hidden;
}

#app {
  min-height: 100vh;
}

.router-view-container {
  padding-top: 80px;
  min-height: calc(100vh - 80px);
}

.router-view-container.no-navbar {
  padding-top: 0;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-weight: bold;
  font-size: 2.2em;
}

p, div, span, input, textarea, button {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.2em;
}

/* Element UI v-modalz-index */
.v-modal {
  z-index: 1 !important;
}

/*  */
a, button, .el-menu-item {
  cursor: pointer;
}
</style>
